import React, {Component} from 'react';


export class Add extends Component {

    state = {
        title: '',
        desc: ''
    }

onChangeTitle = (e) => {
        this.setState(
            {
                title: e.target.value,
            }
        )
}

onChangeDesc = (e) => {
        this.setState(
            {
                desc: e.target.value,
            }
        )
    }

    onSubmit = (e) => {
        e.preventDefault();
        this.props.AddAnnounc(this.state.title, this.state.desc);
        this.setState( {
            title: '',
            desc: ''
        });
    }

    render() {
        return (
            <div className="Add">
                <h2>Add announcement</h2>
                <form onSubmit={this.onSubmit}>
                    <label>Title</label><input type='text' onChange={this.onChangeTitle} value={this.state.title}/><br/>
                    <label>Description</label><textarea  onChange={this.onChangeDesc} value={this.state.desc}/><br/>

                    <button type='submit'>Add</button>
                </form>
            </div>
        );
    }
}
