import React from 'react';
import AnnounsmentItem from './AnnounsmentItem';


export const AnnounsmentList = ({announcements, onDeleted, onEdit}) => {

    return (
        <div>

            <ul>
                {announcements.map(announcement =>
                    (<AnnounsmentItem
                        announcement={announcement}
                    onDeleted={() => onDeleted(announcement.id)}
                        onEdit={() => onEdit(announcement.id,announcement.title,announcement.description,announcement.date)}/>))}

            </ul>

        </div>
    );

}
