import React, {Component} from 'react';
import './App.css';
import {Search} from './components/Search';
import Header from './components/Header';
import {Add} from './components/Add';
import {AnnounsmentList} from './components/AnnounsmentList';
import {Edit} from './components/Edit';

export default class App extends Component {
state = {
    announcements : [{
        id:'0000',
        title: '1',
        description: 'lorem10',
        date: '22.10.1985'
    },
        {id:'0001',
            title: '11',
            description: 'lorem101111',
            date: '22.09.1985'
        }],
    tempEditing: {},
}
delItem = (id) => {
    this.setState(
        ({announcements}) => {
        
            return{
                announcements: announcements.filter(a => a.id!==id),
            };

        }
    );
}
    addItem = (title, description) => {
        let date = new Date();
        let data = date.getDate()+'.0'+
            date.getMonth()+'.'+date.getFullYear()+'  '+date.getHours()+':'+date.getMinutes()+':'+date.getSeconds();
    const newAnnounsment = {
        id: Date.now(),
        title: title,
        description: description,
        date: data}
        this.setState(
            ({announcements}) => {

                return{
                    announcements: [...announcements, newAnnounsment],
                };

            }
        );
    }

   editItem = (id, title, description, date) => {

        this.setState(
            ({tempEditing}) => {

const newData ={...tempEditing, id: id, title: title, description:description, date: date};
                console.log(newData);
                return{
                    tempEditing: newData,
                };

            }
        );
    }
    render(){

        return (
            <>
                <div className="Header">
                    <Header />
                </div>
                <div className="Search">
                    <Search/>
                </div>
            <div className="App">
               <Add AddAnnounc={this.addItem}/>
               <AnnounsmentList
                   announcements={this.state.announcements}
                   onDeleted={this.delItem}
                onEdit={this.editItem}/>
                    <Edit />

            </div>
            </>
        );
    }}
